@import '../../vars';

.slider {
  position: sticky;
  top: 25px;
}

.slider__wrapper-outer {
  overflow: hidden;
  min-height: 1px;
}

.slider__wrapper {
  display: flex;
  flex-wrap: nowrap;
  transition: all 0.3s;
}

.slider__main {
  margin-bottom: 5px;
  position: relative;
  overflow: hidden;
  img {
    max-height: 700px;
  }
}

.slider__thumbs {
  .slider__item {
    cursor: pointer;
    &--active {
      &::before {
        content: '';
        display: block;
        width: calc(100% - 5px);
        height: 100%;
        border: 3px solid $color-secondary;
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }
}

.slider__item {
  flex-shrink: 0;
  position: relative;
  img {
    display: block;
    padding-right: 5px;
    width: 100%;
  }
}

.slider__button {
  padding: 10px;
  background: transparent;
  position: absolute;
  z-index: 1;
  top: 50%;
  transform: translateY(-50%);
  border: none;
  cursor: pointer;
  opacity: 0.7;
  transition: opacity 0.3s;
  &:hover {
    opacity: 1;
  }
  svg path {
    color: $white;
    filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, 0.4));
  }
}

.slider__prev-button {
  left: 0;
}

.slider__next-button {
  right: 5px;
}

.slider__full-screen-button {
  left: 50%;
  transform: translateX(-50%);
  bottom: -100px;
  top: auto;
  opacity: 0;
  transition: bottom 0.3s, opacity 0.3s;
  &--active {
    bottom: 10px;
    opacity: 1;
  }
}

.slider__disable-full-screen-button {
  right: 20px;
  left: auto;
  top: 20px;
  transform: none;
}

.slider--full-screen {
  @media screen and (min-width: $screen-md) {
    overflow-y: auto;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1000;
    width: 100%;
    height: 100%;
    background-color: $white;
    display: flex;
    flex-direction: column;
    .slider__main {
      flex-grow: 1;
      .slider__item {
        display: flex;
        align-items: center;
      }
      img {
        width: auto;
        margin: 0 auto;
        height: 100%;
      }
    }
    .slider__item {
      height: 100%;
    }
    .slider__wrapper-outer {
      height: 100%;
    }
    .slider__wrapper {
      height: 100%;
    }
  }
}

@media screen and (max-width: $screen-sm - 1px) {
  .slider {
    padding-left: 2.5px;
    position: static;
  }

  .slider__thumbs {
    .slider__item--active {
      &::before {
        border-width: 2px;
      }
    }
  }

  .slider__button {
    svg {
      max-width: 20px;
    }
  }

  .slider__full-screen-button {
    display: none;
  }

  .slider__disable-full-screen-button {
    display: none;
  }
}
